body {
  margin: 0;
  padding: 0;
  /* background: #444; */
  font-family: 'Merriweather Sans', sans-serif;
}

.container {
  height: calc(100vh - 60px);
  display: -webkit-flex;
  display: flex;
  background-color: #FFF;
}

h2 {
  color: #1890ff;
  margin: 0px !important;
  height: 60px;
  line-height: 60px;
  border-bottom: 1px solid #DDD
}

h2.head {
  padding: 0px 10px;
  font-weight: 700;
}

.ant-form-item {
  margin-bottom: 5px;
}

button + button {
  margin-left: 10px;
}
.buildings, .rooms {
  width: 300px;
  max-width: 30%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  position: relative;
}
.buildingItem {
  cursor: pointer;
  background: #fff;
  border-bottom: 1px solid #ddd;
  padding: 10px;
}
.buildingItem.selected {
    background: #EEE
}
.bHeader {
  font-size: 20px;
  display: block;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
.buildings-group {
    overflow: auto
}
.addBuildingFrm, .addRoomFrm {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    margin: 10px;
}
.rooms {
  width: 300px;
  max-width: 30%;
  border:1px solid #DDD;
  border-top: none;
  border-bottom: none; 
  position: relative;
}
.editor {
  -webkit-flex-grow: 1;
          flex-grow: 1;
  padding: 20px;
  overflow: auto;
  position: relative;
}

.faded {
    font-style: italic;
    color: #999
}

.empty {
  position: absolute;
  top: 66px;
  left: 10px;
  font-size: 20px;
  color: #AAA;
}
